import React,{useEffect} from 'react'
import IMG0 from '../../statics/rules0.jpg';
import PlayList from './component/playlist';
import { playListObj } from './component/playListData';
import {
    RoomsWrapprr,
    RoomsContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper
} from './style'
const HowToPlay = () => {
    useEffect(()=>{
        window.scrollTo(0, 0)
      },[])
    return (
        <>
        <RoomsWrapprr>
            <RoomsContainer>
                <TopWrapper>
                    <TitleWrapper><Title>西螺好吃好玩</Title></TitleWrapper>
                    <ImgWrapper src={IMG0}/>
                </TopWrapper>
                <BottomWrapper>
                    <PlayList {...playListObj}/>
                </BottomWrapper>
            </RoomsContainer>
        </RoomsWrapprr>
    </>
    )
}

export default HowToPlay
