import { useState, useEffect } from 'react';
import './App.css';
import Header from './common/header';
import SideBar from './common/sidebar';
import Footer from './common/footer';
import Home from './pages/home';
import Rules from './pages/rules';
import Roomintroduction from './pages/roomintroduction';
import Journey from './pages/journey';
import HowToPlay from './pages/howtoplay';
import Transportation from './pages/transportation';
import { BrowserRouter as Router, Route } from 'react-router-dom';
const App = () => {
  const [isOpen, setOpen] =  useState(false);

  useEffect(()=>{
    if(isOpen){
      document.body.style.overflow = 'hidden';
    }else{
      document.body.style.overflow = 'unset';
    }
  },[isOpen])

  return (
    <Router>
      <SideBar isOpen={isOpen} setOpen={setOpen}/>
      <Header isOpen={isOpen} setOpen={setOpen}/>
      <Route path='/' exact component={Home} />
      <Route path='/rules' exact component={Rules} />
      <Route path='/roomintroduction/:id' exact component={Roomintroduction} />
      <Route path='/journey' exact component={Journey} />
      <Route path='/howtoplay' exact component={HowToPlay} />
      <Route path='/transportation' exact component={Transportation} />
      <Footer/>
    </Router>
  );
}

export default App;
