import React,{useEffect} from 'react'
import IMG0 from '../../statics/rules0.jpg';
import JourneyItem from './component/journeyItem'
import {
    JourneyObj1,
    JourneyObj2,
    JourneyObj3
}  from './component/journeyData';
import {
    RoomsWrapprr,
    RoomsContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper
} from './style'
const Journey = () => {
    useEffect(()=>{
        window.scrollTo(0, 0)
      },[])
    return (
        <>
        <RoomsWrapprr>
            <RoomsContainer>
                <TopWrapper>
                    <TitleWrapper><Title>套裝行程</Title></TitleWrapper>
                    <ImgWrapper src={IMG0}/>
                </TopWrapper>
                <BottomWrapper>
                    <JourneyItem {...JourneyObj1} />
                    <JourneyItem {...JourneyObj2} />
                    <JourneyItem {...JourneyObj3} />
                </BottomWrapper>
            </RoomsContainer>
        </RoomsWrapprr>
    </>
    )
}

export default Journey
