import React,{useEffect} from 'react'
import IMG0 from '../../statics/rules0.jpg';
import HomestaySection from './components/homestaySection';
import HomeStay from './components/homeStay';
import { Route } from 'react-router-dom';
import {
    HomeStayObj1,
    HomeStayObj2,
    HomeStayObj3
} from './components/homestaySectionData';
import {
    RoomsWrapprr,
    RoomsContainer,
    TopWrapper,
    TitleWrapper,
    Title,
    ImgWrapper,
    BottomWrapper
} from './style'
const Roomintroduction = () => {
    useEffect(()=>{
        window.scrollTo(0, 0)
      },[])
    return (
        <>
            <RoomsWrapprr>
                <RoomsContainer>
                    <Route exact path='/roomintroduction/default'>
                        <TopWrapper>
                            <TitleWrapper><Title>房型介紹</Title></TitleWrapper>
                            <ImgWrapper src={IMG0}/>
                        </TopWrapper>
                        <BottomWrapper>
                            <HomestaySection {...HomeStayObj1}/>
                            <HomestaySection {...HomeStayObj2}/>
                            <HomestaySection {...HomeStayObj3}/>
                        </BottomWrapper>
                        </Route>
                        <Route path='/roomintroduction/homeStay1'>
                            <HomeStay {...HomeStayObj1}/>
                        </Route>
                        <Route path='/roomintroduction/homeStay2'>
                            <HomeStay {...HomeStayObj2}/>
                        </Route>
                </RoomsContainer>
            </RoomsWrapprr>
        </>
    )
}

export default Roomintroduction
