import React from 'react'
import { Carousel } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import IMG1 from '../../../statics/background1.jpg';
import IMG2 from '../../../statics/background2.jpg';
import IMG3 from '../../../statics/background3.jpg';
import './mainsection.css'
const Mainsection = () => {
    return (
        <>           
            <Carousel className='test' >
            {/* style={{height: '580px',width: '100%',overflow:'hidden',margin:'0 auto',}} */}
                <Carousel.Item interval={1000}>
                    <img
                    className="d-block w-100 CarouselImg"
                    src={IMG1}
                    alt="First slide"
                    // style={{height: '580px',width: '100%'}}
                    />
                    <Carousel.Caption>
                    {/* <h3>First slide label</h3>
                    <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p> */}
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item interval={500}>
                    <img
                    className="d-block w-100 CarouselImg"
                    src={IMG2}
                    alt="Second slide"
                    // style={{height: '580px',width: '100%'}}
                    />
                    <Carousel.Caption>
                    {/* <h3>Second slide label</h3>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p> */}
                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                    className="d-block w-100 CarouselImg"
                    src={IMG3}
                    alt="Third slide"
                    // style={{height: '580px',width: '100%'}}
                    />
                    <Carousel.Caption>
                    {/* <h3>Third slide label</h3>
                    <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p> */}
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </>
    )
}

export default Mainsection
